
import { defineComponent, ref, computed, ComputedRef } from "vue";

export default defineComponent({
  props: {
    state: Boolean,
    iconName: String,
    name: String,
  },
  setup(props) {
    const statusText: ComputedRef<string> = computed((): string =>
      props.state ? "ON" : "OFF"
    );

    return {
      statusText,
    };
  },
});
